.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.navbar ul{
  display: flex;
  gap: 2vh;
  list-style: none;
}
.imagen{
  border-radius: 50%;
  object-fit: contain;
  width: 250px;
  height: 250px;
}
.formulario{
  padding: 2vh;
  border: solid 1px;
  width: fit-content;
  border-radius: 5%;
  display: grid;
  place-items: center;
}

.formulario input{
  margin: 0.5vh;
}

.formulario button{
  display: block;
  border-radius: 5px;
  width: 50%;
  margin: 10px auto;
  padding: 1vh;
}

.content{
  display: flex;
  flex-direction: column;
  align-items: center;
}